.header-bg {
    background-color: var(--c12);
}

.header {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

.header-menu {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
}

.header-menu a {
    color: var(--c0);
    display: inline-block;
    padding: 16px 0;
    position: relative;
}

.header-menu a:after {
    content: "";
    display: block;
    height: 2px;
    width: 0px;
    background: var(--c0);
    margin-top: 4px;
    transition: 0.3s;
    position: absolute;
}

.header-menu a:hover:after {
    width: 100%;
}

/*
MEDIA QUERIES
*/

@media (max-width: 800px) {
    
    .header-menu {
        gap: 20px;
    }
    
    .header-menu a {
        background-color: var(--c11);
        padding: 12px 16px;
        border-radius: 5px;
    }

    .header-menu a:hover {
        background-color: var(--c10);
    }

    .header-menu a:after {
        display: none;
    }
}

@media (max-width: 600px) {
    .header-menu {
        gap: 12px;
    }

    .header-menu a {
        padding: 8px 12px;
        font-size: 14px;
    }
}
