.font-1-xxl {
    font: 600 4rem/1.125 "Poppins", sans-serif;
}

.font-1-xl {
    font: 600 2rem/1.25 "Poppins", sans-serif;
}

.font-1-l {
    font: 400 1.5rem/1.5 "Poppins", sans-serfi;
}

.font-2-l {
    font: 400 1.5rem/1.5 "Roboto", sans-serif;
}

.font-2-l-b {
    font: 500 1.5rem/1.5 "Roboto", sans-serif;
    line-height: 0.015em;
    text-transform: uppercase;
}

.font-1-m {
    font: 500 1.125rem/1.33 "Poppins", sans-serif;
}

.font-1-m-b {
    font: 600 1.125rem/1.33 "Poppins", sans-serif;
}

.font-2-m {
    font: 500 1.125rem/1.33 "Roboto", sans-serif;
}

.font-1-s {
    font: 400 1rem/1.5 "Poppins", sans-serif;
}

.font-2-s {
    font: 400 1rem/1.5 "Roboto", sans-serif;
}

.font-1-xs {
    font: 600 0.75/1.33 "Poppins", sans-serif;
}

.font-2-xs {
    font: 400 0.875/1.42 "Roboto", sans-serif;
}

/* MEDIA QUERIES */

@media (max-width: 1200px) {
    .font-1-xxl {
        font-size: 3rem;
    }
    .font-2-l, .font-2-l-b {
        font-size: 1.125rem;
    }
}

@media (max-width: 800px) {
    .font-1-xxl {
        font-size: 2rem;
    }
    .font-1-xl, .font-2-xl {
        font-size: 1.5rem;
    }
}