.tecnologia-bg {
    background-color: var(--c11);
    box-shadow: inset 0 80px var(--c0), inset 0 -80px var(--c0);
}

.tecnologia {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
}

.tecnologia-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: left;
}

.tecnologia-conteudo {
    padding-top: 160px;
    padding-bottom: 160px;
}

.tecnologia-conteudo > span {
    display: block;
    margin-bottom: 20px;
}

.tecnologia-conteudo h2 {
    margin-bottom: 32px;
}

.tecnologia-conteudo > p {
    margin-bottom: 20px;
}

.tecnologia a {
    margin-bottom: 80px;
}

.tecnologia-vantagens {
    display: flex;
    gap: 40px;
}

.tecnologia-vantagens h3 {
    margin-bottom: 8px;
    width: max-content;
}

.tecnologia-vantagens img {
    width: 24px;
    margin-bottom: 8px;
}

@media (max-width: 800px) {
    .tecnologia-bg {
        box-shadow: initial;
    }
    .tecnologia {
        grid-template-columns: 1fr;
    }
    .tecnologia-conteudo {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .tecnologia-img {
        display: none;
    }
    .tecnologia a {
        margin-bottom: 60px;
    }
}

@media (max-width: 600px) {
    .tecnologia-vantagens {
        flex-direction: column;
    }
}