/*RESET*/
body {
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    height: 2000px;
    color: #111111;
}

h1, h2, h3, ul, p, blockquote {
    margin: 0;
}

ul {
    padding: 0;
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

img {
    max-width: 100%;
    display: block;
}

img[src$=".jpg"] {
    border-radius: 5px;
}
